.hero-image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hero-image {
  width: 100%;
  filter: drop-shadow(0px 1px 3px rgba(0,0,0, .25));
  margin-top: 3em;
}

.hero-image:hover {
  cursor: pointer;
}

@media screen and (max-width: 1200px) {
  .hero-image {
    width: 80%;
    margin: 0 auto;
    margin-top: 6em;
  }
}